import "bootstrap/scss/bootstrap.scss";
import 'bootstrap/dist/css/bootstrap.css';
import React from "react";
import { createRef, useRef } from "react";
// reactstrap components
import {
  Container,
  Row,
  Col,
  Button
} from "reactstrap";
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import image from '../../assets/img/login-image.jpg'


// core components

function Slide8(props) {
  let pageHeader = React.createRef();
  const { contactUsRef } = props
  React.useEffect(() => {
    if (window.innerWidth < 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });



  const executeScroll = () => contactUsRef.current.scrollIntoView({ behavior: "smooth" });
  return (
    <div style={{height:'100vh',width:'100%'}}>
      <div style={{ height: '45%' ,width:'100%', backgroundImage: `url(${image})`,}} className="d-flex justify-content-center align-items-end">
      <Container>
          <Row style={{marginBottom:'15px'}}>
          <h2 style={{ color: 'white', fontWeight: 'bold' }}>
          CHEMICAL LOGISTICS
              </h2>
           
    </Row>
    </Container>
      </div>
      <div
        style={{height: '35%'}}
      >
        <Container>
          <Row>
   
            <Col md={'12'}>
              <h5 style={{ color: 'black' ,marginTop:'15px'}}>
              It is our goal to harmonize our services with your specifications in such a way that you obtain the maximum value added  whether the services consist of chemical transport operations, warehousing for dangerous substances for the chemical or the oil and gas industries.
              </h5>  </Col>
          </Row>
          <Button
                            // onClick={() => {
                            //   executeScrollSlide5()
                            //   // executeScroll()
                            // }}
                            //   href="#ContactUs"
                            className="btn-round"
                            color="success"
                            target="_blank"
                            size="medium"
                            outline
                            style={{fontSize:8,marginRight:'10px'}}
                          > Read More <ArrowForwardIcon fontSize={'small'}/></Button>
        </Container>
    
      </div>


      </div>
  );
}

export default Slide8;
